import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';
import DogStatus from '../../components/dog/status';

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		this.setState({ isLoading: true });
		await this.loadDogs();
		this.setState({ isLoading: false });
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }
    componentDidMount() {
        this.loadAll();
    }

	render() {
		const { dogStore, appState, userStore } = this.props.stores;
        const { darkmode } = appState;
		const { isAdmin } = userStore;

		const dogsMaturity = dogStore.findDogsByStatus(10);
		const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		const dogsWithNeeds = dogStore.findDogsWithNeeds();

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					{isAdmin && <div class='col text-center subtopmenu'>
						<a href='/admin' class='text-secondary stretched-link'><nobr><i class='fas fa-user-shield' /> <Text id='home.admin'>Admin</Text></nobr></a>
					</div>}
					<div class='col text-center subtopmenu'>
						<a href='/' class='text-secondary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='todo.dashboard'>Dashboard</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/feed' class='text-secondary stretched-link'><nobr><i class='fas fa-rss' /> <Text id='todo.feed'>Feed</Text></nobr></a>
					</div>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/todo' class='text-primary stretched-link'><nobr><i class='fas fa-list' /> <Text id='todo.todo'>Todo</Text></nobr></a>
					</div>
				</div>

				<div class='row'>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5><Text id='todo.dogs-follow-up'>Hunder som trenger oppflging</Text></h5>
						<div class='row'>
							{dogsWithStatus && dogsWithStatus.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithStatus || dogsWithStatus.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								<Text id='todo.no-dogs-follow-up'>Ingen hunder som trenger ekstra oppflging.</Text>
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5><Text id='todo.dogs-special-needs'>Hunder med spesielle behov</Text></h5>
						<div class='row'>
							{dogsWithNeeds && dogsWithNeeds.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithNeeds || dogsWithNeeds.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								<Text id='todo.no-dogs-special-needs'>Ingen hunder med spesielle behov.</Text>
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5><Text id='todo.dogs-with-maturity'>Hunder med lpetid</Text></h5>
						<div class='row'>
							{dogsMaturity && dogsMaturity.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsMaturity || dogsMaturity.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								<Text id='todo.no-dogs-with-maturity'>Ingen hunder med lpetid.</Text>
							</div>}
						</div>
					</div>					

				</div>
			</div>
		);
	}
}

export default Home;
